import { Breadcrumb } from "../../components"
import { ConstTranslate } from "../../constants/translate"
import { useForm } from "react-hook-form";

import { ReactComponent as LockIcon } from "../../assets/icon/lock.svg"
import { ReactComponent as CalendarIcon } from "../../assets/icon/calendar.svg"
import { ReactComponent as CreditCardIcon } from "../../assets/icon/credit_card.svg"
import { ReactComponent as UserProfileIcon } from "../../assets/icon/user_profile.svg"



const CreatePayment = () => {
    const { register, handleSubmit, formState: { errors } } = useForm();
    const onSubmit = data => console.log(data);

    console.log('errors', errors)
    return (
        <div className="container backgrounds__white-top">
            <Breadcrumb pageTitle={ConstTranslate.PAYMENT} />

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className="flex gap--15 f-dir-col mx-4">
                    <div className="input-container">
                        <span className="input__icon"><UserProfileIcon /></span>
                        <input className="input" type="text" placeholder="Card holder name" {...register("cardHolderName")} />
                    </div>

                    <div className={`input-container ${errors?.creditNumber ? "input-error" : ""}`}>
                        <span className="input__icon"><CreditCardIcon /></span>
                        <input className="input" type="password" placeholder="Card Number" {...register("creditNumber", { pattern: /^(?:[0-9]{4}-){3}[0-9]{4}$|^[0-9]{16}$/ })} />
                    </div>


                    <div className="flex gap--10">
                        <div className="input-container">
                            <span className="input__icon"><CalendarIcon /></span>
                            <input className="input" type="date" placeholder="Card holder name" {...register("cardExpirationDate")} />
                        </div>

                        <div className="input-container">
                            <span className="input__icon"><LockIcon /></span>
                            <input className="input" type="number" placeholder="CVV" {...register("cardCvv")} />
                        </div>
                    </div>
                </div>

                <div className="singel-button--container">
                    <button className="button button__h-60">
                        {ConstTranslate.ADD_NEW_CARD}
                    </button>
                </div>
            </form>
        </div>
    )
}

export default CreatePayment